.aboutUsHistory__section {
  background-color: #f4f5f6;
  padding: 100px 0;
}

.aboutUsHistory__title {
  color: #1e212c;

  font-family: Ubuntu;
  font-size: 46px;
  font-style: normal;
  font-weight: 700;
  line-height: 130%;

  @media (max-width: 620px) {
    text-align: center;
  }
}

.aboutUsHistory__wrapper--slide {
  max-width: 810px;
  width: 100%;
}

.aboutUsHistory__slide--img {
  border-radius: 4px;
  object-fit: contain;
  @media (max-width: 620px) {
    width: 600px;
  }
  @media (max-width: 480px) {
    width: 450px;
  }
  @media (max-width: 380px) {
    width: 320px;
  }
}

.aboutUsHistory__slide--text {
  margin-top: 46px;
  color: #424551;
  font-family: Ubuntu;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
}

.aboutUsHistory__slide .slick-dots li {
  border: none;
  opacity: 1;
  width: 250px;
  height: 42px;

  @media (max-width: 868px) {
    width: 160px;
  }
}

.aboutUsHistory__slide .slick-dots li:not(:first-child) {
  margin-top: 20px;
}

.aboutUsHistory__slide .slick-dots {
  position: static;
}

.aboutUsHistory__slide .slick-list {
  margin-left: 130px;

  @media (max-width: 1200px) {
    margin-left: 15px;
  }
  @media (max-width: 620px) {
    margin-left: 0;
  }
}

.aboutUsHistory__slide .custom-dots {
  padding: 0;
  display: flex;
  flex-direction: column;

  @media (max-width: 620px) {
    display: none;
  }
}

.aboutUsHistory__slide .custom-dots--itenm:not(:first-child) {
  margin-top: 20px;
}

.aboutUsHistory__slide .custom-dot {
  display: flex;
  align-items: center;
}

.aboutUsHistory__slide .custom-dot .active .dot-text {
  color: #333;
}

.customDots__circle {
  position: relative;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #9a9ca5;
}

.customDots__circle::after {
  position: absolute;
  content: "";
  width: 2px;
  height: 62px;
  background-color: #9a9ca5;
  left: 50%;
  transform: translateX(-50%);
  bottom: -30px;
}

.customDots__text {
  margin-left: 32px;
  color: #9a9ca5;

  font-family: Ubuntu;
  font-size: 28px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;

  @media (max-width: 868px) {
    font-size: 18px;
    font-weight: 400;
    margin-left: 10px;
  }
}

.aboutUsHistory__slide .slick-slider {
  margin-top: 60px;
  display: flex;
  flex-direction: row-reverse;
}

.slick-active .customDots__circle {
  background-color: #ff5a30;
}

.slick-active .customDots__circle::after {
  background-color: #ff5a30;
}

.slick-active .customDots__text {
  color: #ff5a30;
}
